import { useRef, useState } from "react";
import "./styles.css";
import Office from "./office.jpg";
import Rooms from "./Rooms";

import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";

const initialState = {
  defaultScale: 0.9,
  limitToBounds: false,
  panningEnabled: true,
  transformEnabled: true,
  pinchEnabled: true,
  limitToWrapper: false,
  disabled: false,
  dbClickEnabled: false,
  lockAxisX: false,
  lockAxisY: false,
  velocityEqualToMove: false,
  enableWheel: true,
  enableTouchPadPinch: true,
  enableVelocity: true,
  limitsOnWheel: false,
  maxScale: 3
};

export default () => {
  const [message, setMessage] = useState("not part of any room");
  const [settings, setSettings] = useState(initialState);
  const {
    limitToBounds,
    panningEnabled,
    transformEnabled,
    pinchEnabled,
    limitToWrapper,
    disabled,
    dbClickEnabled,
    lockAxisX,
    lockAxisY,
    velocityEqualToMove,
    enableWheel,
    enableTouchPadPinch,
    enableVelocity,
    limitsOnWheel,
    maxScale
  } = settings;

  return (
    <div className="center">
      <div className="size">
        <TransformWrapper
          defaultScale={1}
          options={{
            limitToBounds,
            transformEnabled,
            disabled,
            limitToWrapper,
            maxScale
          }}
          pan={{
            disabled: !panningEnabled,
            lockAxisX,
            lockAxisY,
            velocityEqualToMove,
            velocity: enableVelocity
          }}
          pinch={{ disabled: !pinchEnabled }}
          doubleClick={{ disabled: !dbClickEnabled }}
          wheel={{
            wheelEnabled: enableWheel,
            touchPadEnabled: enableTouchPadPinch,
            limitsOnWheel
          }}
        >
          <TransformComponent>
            <div className="fill">
              <Rooms />
              <img src={Office} alt="big-office" />
            </div>
          </TransformComponent>
        </TransformWrapper>
      </div>
    </div>
  );
};
